.perf-no-animation * {
    transition: none!important;
    -webkit-animation: none!important;
    animation: none!important;
}

html {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 140%;
    background: $white;
    color: $black;
    letter-spacing: normal;
    font-family: 'Inter', sans-serif;
    scroll-padding-top: 56px;
    scroll-behavior: smooth;
}

button:active,
button:hover,
input:active,
input:focus,
input:hover {
    outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul {
    margin: 0;
    padding: 0;
    line-height: 130%;
}

* {
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
    color: #000;
    text-decoration: none;
}

img {
    display: block;
    max-width: 100%;
}

ul {
    list-style-type: none;
}

section {
    position: relative;
    padding: 30px 0;
}

body {
    min-height: 100vh;
    min-height: calc(var(--vh, 1vh) * 100);
    padding-top: 56px;
    width: 100%;
    display: flex;
    flex-direction: column;

    @media screen and (max-width: 1024px) {
        &.mobile{
            overflow: hidden;
        }
        &.mobile::after {
            content: "";
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.5);
            z-index: 999;
            -webkit-animation: 0.3s opacity;
            animation: 0.3s opacity;
        }
        @-webkit-keyframes opacity {
            from {
                opacity: 0;
            }
            to {
                opacity: 1;
            }
        }
        @keyframes opacity {
            from {
                opacity: 0;
            }
            to {
                opacity: 1;
            }
        }
    }
}

footer{
    width: 100%;
    margin-top: auto;
}

.container {
    margin: 0 auto;
    max-width: 1540px;
    padding: 0 50px;
    @media screen and (max-width: 1440px) {
        max-width: 100%;
    }
    @media screen and (max-width: 1024px) {
        padding: 0 25px;
    }
    @media screen and (max-width: 480px) {
        padding: 0 20px;
    }
}

.text-center{
    text-align: center;
}

.cover-image{;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.contain-image{
    width: 100%;
    height: 100%;
    object-fit: cover;
}